.textArea{
    padding-top: 110px;
    padding-bottom: 110px;
    margin-left: 316px;
    margin-right: 494px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100%;

    blockquote{
        font-family: 'Courier', Courier, monospace;
        font-size: 50px;
        margin-bottom: 50px;
        width: 770px;
        text-align: center;
        quotes: "“" "”" "‘" "’";

        &:before {
            content: open-quote;
          }
        &:after {
            content: close-quote;
          }
    }
        
    p{
        width: 770px;
        max-height: 320px;
        margin: 0;
    }

}