$white: #ffffff;
$offwhite: #fdfdfd;
$black: #141414;
$lightgrey: #f4f3ff;
$grey: #808080;

$mainColor: #ffd5f9;
$workColor01: #fff7aa;
$workColor02: #d5ecff;
$workColor03: #628c5e;
$workColor04: #6d8dbc;
$workColor05: #ffd5f9;

* {
  margin: 0;
  padding: 0;
  color: $black;
}

h1 {
  font-family: Courier, monospace;
}

p {
  font-family: Helvetica;
  line-height: 1.5;
  font-size: 20px;
}

.bar {
  height: 10px;
  width: 80px;
  left: -20px;
  margin-top: 40px;
  margin-bottom: 40px;
  background-color: $black;
  position: relative;
}

.barcenter {
  left: 0;
}

.button {
  margin: 0;
  padding-left: 40px;
  padding-right: 40px;
  padding-top: 10px;
  padding-bottom: 10px;
  max-width: max-content;

  border-color: $grey;
  border-width: 4px;
  border-style: solid;
  border-radius: 0;

  text-transform: lowercase;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: bold;
  font-size: 20px;
  color: $grey;

  transition-duration: 0.2s;

  &:hover {
    color: $black;
    border-color: $black;
    text-decoration: none;
    transition-duration: 0.2s;
    padding-left: 34px;
    padding-right: 34px;
    padding-top: 8.5px;
    padding-bottom: 8.5px;
    margin: 1.5px;
    border-radius: 0;
  }
}

.pagesWrapper {
  height: 100vw;
  width: calc(100vh - 80px);
  transform: rotate(-90deg) translateX(-100vh);
  transform-origin: top left;
  overflow-y: scroll;
  overflow-x: hidden;
  position: absolute;
  top: 0px;

  // background-color: $black;

  scrollbar-width: none;
  -ms-overflow-style: none;

  &::-webkit-scrollbar {
    display: none;
  }
}

