.endCard{
    padding-top: 140px;
    padding-bottom: 140px;
    padding-left: 158px;
    padding-right: 158px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    background-color: $lightgrey;
    height: 100%;

    h1{
        font-size: 50px;
        margin-bottom: -10px;
    }

    a{
        font-family: 'Courier', Courier, monospace;
        padding-right: 10px;
        line-height: 1.5em;
        font-size: 25px;
        color: $grey;
        transition-duration: 0.2s;

        &:before{
            content: "→";
            padding-right: 10px;
        }

        &:hover{
            color: $black;
            text-decoration: underline;
            transition-duration: 0.2s;
        }
    }
        
    p{
        max-height: 320px;
        margin: 0;
    }

    @import "contactForm.scss";

}