.titleCardWrapper{
    height: 100%;
    padding-top: 110px;
    padding-bottom: 110px;
    padding-left: 158px;
    padding-right: 158px;
    margin-right: 20px;
    display: flex;
    flex-direction: column;

    .workNr{
        top: -94px;
        left: -135px;
        position: absolute;
        z-index: -1;
        font-weight: bold;
        font-size: 440px;
        letter-spacing: -50px;
    }

    .titleCard{

        h1{
            font-weight: bold;
            font-size: 80px;
            margin-bottom: -25px;
        }
    
        .shortInfo{
            .row{
                display: flex;
                flex-direction: row;
                margin: 0;
    
                .title{
                    font-weight: bold;
                    font-family: 'Courier', Courier, monospace;
                    font-size: 25px;
                    padding-right: 10px;
                    line-height: 1.2;
    
                    &:after{
                        content: ":";
                    }
                }

                .info{
                    font-family: 'Courier', Courier, monospace;
                    font-size: 25px;
                    padding-right: 10px;
                    line-height: 1.2;
                }
            }
        }
            
        .paragraph{
            position: relative;
            max-height: 320px;
            left: 158px;
            top: 50px;
            margin: 0;
            margin-right: 158px;
            // background-color: aquamarine;
        }

        a{
            font-family: 'Courier', Courier, monospace;
            padding-right: 10px;
            line-height: 1.5em;
            font-size: 18px;
            color: $grey;
            transition-duration: 0.2s;
            position: absolute;
            bottom: 110px;z
    
            &:before{
                content: "←";
                padding-right: 10px;
            }
    
            &:hover{
                color: $black;
                text-decoration: underline;
                transition-duration: 0.2s;
            }
        }

        .cv{
            &:before{
                content: none;
            }
        }
    }

}