.card{
    height: calc(100% - 220px);
    margin-top: 110px;
    margin-bottom: 110px;
    padding-top: 100px;
    padding-bottom: 100px;
    padding-left: 20px;
    padding-right: 20px;
    margin-right: 178px;
    background-color: $lightgrey;
    border-radius: 0;
    border: none;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    .title{
        display: flex;
        flex-direction: column;
        align-items: center;

        h1{
            font-size: 40px;
            margin: 0;
            width: 100%;
            text-align: center;
        }
    }
}